@import '../../styles/vars';

.reviews {
  padding: 40px 0;

  &__wrapper {
    max-width: 1240px;
    margin: 0 auto;

    display: flex;
    flex-direction: column;
    align-items: center;
  }

  &__title {
    font-family: 'Fira Sans', sans-serif;
    color: $blue-medium;

    font-size: 2rem;
    font-weight: 700;
    text-align: center;
  }
}